import axios from "axios";

let movieAxiosInstanse = {};

const MovieApiService = {
  init(options) {
    movieAxiosInstanse = axios.create({
      timeout: 5000,
      ...options,
      baseURL: "http://react-cdp-api.herokuapp.com"
    });

    // API doesn't work with single 'filter' query param
    // movieAxiosInstanse.interceptors.request.use(x => {
    //   if (x.params && x.params.filter && !Array.isArray(x.params.filter)) {
    //     x.params.filter = ["Crime"];
    //   }
    //   return x;
    // });

    if (process.env.DEBUG) {
      movieAxiosInstanse.interceptors.request.use(x => {
        console.log(x);
        return x;
      });

      movieAxiosInstanse.interceptors.response.use(x => {
        console.log(x);
        return x;
      });
    }
  },

  getMovies(options) {
    return movieAxiosInstanse
      .get("/movies", {
        params: {
          ...options,
          sortOrder: "desc"
        }
      })
      .then(res => res.data);
  },
  getById(id) {
    return movieAxiosInstanse.get(`/movies/${id}`).then(res => res.data);
  }
};

export default MovieApiService;
